	module.exports = {"data":{"markdownRemark":{"html":"<h2>Font Variant Property</h2>\n<p>The font-variant property is most commonly used to change targeted text to small caps. The default value is <code>normal</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">font-variant</span><span class=\"token punctuation\">:</span> small-caps<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>The property also accepts <code>all-small-caps</code>, <code>petite-caps</code>, <code>all-petite-caps</code>, <code>titling-caps</code>, and <code>unicase</code>; however, these are new in CSS3 and not yet well-supported.</p>\n<p>Note that if you use caps in the HTML markup, that will override the small caps value, resulting in regular uppercase letters. If you want to keep the markup in uppercase, but change to small caps with CSS, just set <code>text-transform: lowercase</code> along with your small caps declaration.</p>\n<p>While small caps can add an extra bit of elegance to your typography, designers reccommend to only use them if they are actually built into the font. “Fake” small caps are computer-generated scaled down versions of capital letters. “Real” small caps, on the other hand, are drawn separately by the type designer and appear a little bit wider and fatter than the “fake” ones. </p>\n<h4>More Information:</h4>\n<p><a href=\"https://designforhackers.com/blog/small-caps/\">Design for Hackers</a></p>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/font-variant\">Font-variant on MDN</a></p>","fields":{"slug":"/css/properties/font-variant-property/"},"frontmatter":{"title":"Font Variant"}}},"pathContext":{"slug":"/css/properties/font-variant-property/"}}

